"use client";

import React, { useState } from "react";
import { FaPen, FaTrash, FaAngleLeft } from "react-icons/fa6";
import NewsList from "./NewsList";
import NewsPagination from "./NewsPagination";
import NewsListChange from "./NewsListChange";
import CreateNews from "./createNews/CreateNews";

const News = () => {
  const [view, setView] = useState<"cards" | "list">("cards");
  const [showCreateNews, setShowCreateNews] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const [page, setPage] = useState<number>(1);
  const [total, setTotal] = useState<number>(0);

  const handleViewChange = (view: "cards" | "list") => {
    setView(view);
  };

  const handleNewsCreated = () => {
    setShowCreateNews(false);
    setShowToast(true);
    setTimeout(() => setShowToast(false), 3000);
  };

  return (
    <div className="flex flex-col md:items-start gap-5">
      {showCreateNews ? (
        <div className="flex flex-col items-start gap-5">
          <button
            onClick={() => setShowCreateNews(false)}
            className="btn btn-ghost"
          >
            <FaAngleLeft />
            Tilbage
          </button>
          <CreateNews onNewsCreated={handleNewsCreated} />
        </div>
      ) : (
        <>
          <div className="flex justify-between items-center w-full">
            <button
              onClick={() => setShowCreateNews(true)}
              className="btn btn-primary"
            >
              Opret nyhed
            </button>
            <NewsListChange onViewChange={handleViewChange} />
          </div>
          <NewsList
            view={view}
            page={page}
            setPage={setPage}
            setTotal={setTotal}
          />
          <div className="flex w-full justify-center mt-5">
            <NewsPagination page={page} setPage={setPage} total={total} />
          </div>
        </>
      )}
      {showToast && (
        <div className="toast toast-end">
          <div className="alert alert-success">
            <span>Nyhed oprettet</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default News;
