import React, { useState, useEffect } from "react";
import Image from "next/image";
import { FaPen, FaTrash } from "react-icons/fa6";
import { getAllNews } from "@/lib/server/actions";
import NewsPagination from "./NewsPagination";

interface NewsListProps {
  view: "cards" | "list";
  page: number;
  setPage: (page: number) => void;
  total: number;
  setTotal: (total: number) => void;
}

interface NewsItem {
  id: number;
  title: string;
  desc: string | null;
  formType: "normal" | "beforeAfter";
  image: string | null;
  imageBefore: string | null;
  imageAfter: string | null;
}

const FALLBACK_IMAGE = "/demo.webp";

const NewsList = ({ view, page, setPage, total, setTotal }: NewsListProps) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [newsItems, setNewsItems] = useState<NewsItem[]>([]);

  useEffect(() => {
    const fetchNews = async () => {
      try {
        setLoading(true);
        const { news, total } = await getAllNews(page);
        console.log("Fetched News Data:", news); // Log data her
        setNewsItems(news || []);
        setTotal(total);
      } catch (error) {
        console.error("Failed to fetch news:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchNews();
  }, [page, setTotal]);

  const truncateDescription = (
    description: string | null,
    maxLength: number
  ) => {
    if (!description || description.length <= maxLength)
      return description || "";
    return description.substring(0, maxLength) + "...";
  };

  return (
    <div className="w-full">
      {loading ? (
        <div className="flex justify-center gap-3 items-center">
          <span className="loading loading-spinner loading-md"></span>
          Indhenter data...
        </div>
      ) : (
        <>
          {view === "cards" ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-5">
              {newsItems.map((item) => (
                <div
                  key={item.id}
                  className="card card-compact shadow-lg rounded-md"
                >
                  <figure>
                    {item.formType === "beforeAfter" ? (
                      <div className="diff aspect-[16/9]">
                        <div className="diff-item-1">
                          <Image
                            src={item.imageBefore || FALLBACK_IMAGE}
                            alt={`${item.title} før`}
                            width={500}
                            height={300}
                            style={{ objectFit: "cover" }}
                          />
                        </div>
                        <div className="diff-item-2">
                          <Image
                            src={item.imageAfter || FALLBACK_IMAGE}
                            alt={`${item.title} efter`}
                            width={500}
                            height={300}
                            style={{ objectFit: "cover" }}
                          />
                        </div>
                        <div className="diff-resizer"></div>
                      </div>
                    ) : (
                      <div className="relative w-full h-0 pb-[56.25%] overflow-hidden">
                        <Image
                          src={item.image || FALLBACK_IMAGE}
                          alt={item.title}
                          fill
                          sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                          style={{ objectFit: "cover" }}
                        />
                      </div>
                    )}
                  </figure>
                  <div className="card-body">
                    <h2 className="card-title text-lg">{item.title}</h2>
                    <p className="text-xs">
                      {truncateDescription(item.desc, 100)}
                    </p>
                    <div className="card-actions justify-end mt-2">
                      <button className="btn btn-sm">
                        <FaPen />
                        <span className="md:flex hidden"> Rediger </span>
                      </button>
                      <button className="btn btn-sm">
                        <FaTrash />
                        <span className="md:flex hidden"> Slet </span>
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <ul className="flex flex-col gap-3">
              {newsItems.map((item) => (
                <React.Fragment key={item.id}>
                  <li>
                    <div className="flex justify-between items-center">
                      <div className="flex gap-2 items-center">
                        <div className="relative w-14 h-10 rounded-md overflow-hidden">
                          <Image
                            src={
                              item.formType === "beforeAfter"
                                ? item.imageBefore ||
                                  item.imageAfter ||
                                  FALLBACK_IMAGE
                                : item.image || FALLBACK_IMAGE
                            }
                            alt={item.title}
                            fill
                            style={{ objectFit: "cover" }}
                            sizes="(max-width: 200px) 100vw, (max-width: 500px) 50vw, 33vw"
                          />
                        </div>
                        <h3 className="font-semibold text-sm">{item.title}</h3>
                      </div>
                      <div className="flex gap-5 md:gap-2">
                        <button className="btn btn-sm">
                          <FaPen />
                          <span className="md:flex hidden"> Rediger </span>
                        </button>
                        <button className="btn btn-sm">
                          <FaTrash />
                          <span className="md:flex hidden"> Slet </span>
                        </button>
                      </div>
                    </div>
                  </li>
                  <hr className="border-[1px] rounded-lg border-base-200" />
                </React.Fragment>
              ))}
            </ul>
          )}
          <NewsPagination page={page} setPage={setPage} total={total} />
        </>
      )}
    </div>
  );
};

export default NewsList;
